<script>
import Footer from './common/footer.vue'
export default {
    name:'Home',
    components:{
        Footer
    },
    data(){
        return{
            classNum:1
        }
    },
    mounted: function () {
        this.$nextTick(function () {
            window.addEventListener('scroll', this.onScroll)
        })
    },
    methods: {
        goAnchor (selector,index) {
            this.classNum = index;
            let jump = this.$el.querySelector(selector);
            let total = jump.offsetTop-500;
            let distance = document.documentElement.scrollTop || document.body.scrollTop;
            // 500ms10ms50
            let step = total / 50
            if (total > distance) {
                smoothDown()
            } else {
                let newTotal = distance - total
                step = newTotal / 50
                smoothUp()
            }
            function smoothDown () {
                if (distance < total) {
                    distance += step
         document.body.scrollTop = distance
                    document.documentElement.scrollTop = distance
                    setTimeout(smoothDown, 15)
                } else {
                    document.body.scrollTop = total
                    document.documentElement.scrollTop = total
                }
            }
            function smoothUp () {
                if (distance > total) {
                    distance -= step
        document.body.scrollTop = distance
                    document.documentElement.scrollTop = distance
                    setTimeout(smoothUp, 15)
                } else {
                    document.body.scrollTop = total
                    document.documentElement.scrollTop = total
                }
            } 
        },
        onScroll () {
            let scrolled = document.documentElement.scrollTop || document.body.scrollTop
         // 5861063
            if (scrolled >= 4400) {
                this.classNum = 6
            } else if (scrolled < 4400 && scrolled >= 3405) {
                this.classNum = 5
            } else if (scrolled < 3405 && scrolled >= 2451) {
                this.classNum = 4
            } else if (scrolled < 2451 && scrolled >= 1464) {
                this.classNum = 3
            } else if (scrolled < 1464 && scrolled >= 553) {
                this.classNum = 2
            } else {
                this.classNum = 1
            }
        }
    }

}
</script>
